import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Headertwo from "../components/Headertwo";
import { Footer } from "../components/Footer";
import { Createnewdate } from "../components/Createnewdate";
import { Editnewdate } from "../components/Editnewdate";
import ev1 from "../assets/images/ev1.png";
import ev2 from "../assets/images/ev2.png";
import axios from "axios";
import { useTranslation } from "react-i18next";
import i18n from "../i18n";
import { AlertDelete } from "../components/AlertDelete";
import { RiHome6Line } from "react-icons/ri";
import { IconHome } from "@tabler/icons-react";
export default function Speeddate() {
  var apiUrl = "https://backend.amourette.no/api/profile/";
  const [activeIndex, setActiveIndex] = useState(0);
  const [isallspeedDate, setallspeedDate] = useState([]);
  const [searchTerm, setsearchTerm] = useState("");
  const [filters, setFilters] = useState([]);
  const [editDelete, seteditDelete] = useState(false);
  const [deleteMessage, setdeleteMessage] = useState("");
  const [isPopupOpen_delete, setisPopupOpen_delete] = useState(false);
  const [speedId, setspeedId] = useState("");
  const [speedIdedit, setspeedIdedit] = useState("");
  const [speedDateEdit, setspeedDateEdit] = useState("");
  const handleButtonClick = (index) => {
    setActiveIndex(index);
  };

  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [isPopupOpen1, setIsPopupOpen1] = useState(false);

  const openPopup = () => {
    setIsPopupOpen(true);
    document.body.style.overflowY = "hidden";
  };
  const openPopup1 = () => {
    setIsPopupOpen1(true);
    document.body.style.overflowY = "hidden";
  };
  const closePopup1 = () => {
    setIsPopupOpen1(false);
    document.body.style.overflowY = "auto";
  };
  const closePopup = () => {
    setIsPopupOpen(false);
    document.body.style.overflowY = "auto";
  };
  useEffect(() => {
    getAlldates();
  }, []);
  const getAlldates = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrl + "getdates", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;

      setallspeedDate(respo.results);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const calculateAge = (birthday) => {
    const birthDate = new Date(birthday);
    const today = new Date();

    let age = today.getFullYear() - birthDate.getFullYear();
    const monthDifference = today.getMonth() - birthDate.getMonth();

    // Adjust age if the birthday hasn't occurred yet this year
    if (
      monthDifference < 0 ||
      (monthDifference === 0 && today.getDate() < birthDate.getDate())
    ) {
      age--;
    }

    return age;
  };
  const setSearchTerm = async (e) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    setsearchTerm(e);
    let formData = {
      search: e,
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrl + "getdatesSearch", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data.results;
      console.log(respo);
      setallspeedDate(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };
  const refreshpage = async () => {
    getAlldates();
  };
  const { t } = useTranslation();

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved
    console.log("language");
    console.log(savedLanguage);

    // Change the language if it's not already the current language
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);
  useEffect(() => {
    // Define an async function inside useEffect
    const fetchData = async () => {
      const storedUsername = localStorage.getItem("userLogin");
      const userLogin = storedUsername ? JSON.parse(storedUsername) : null;

      let formData = {
        search: filters,
        user_id: userLogin ? userLogin.id : null, // Handle the case when userLogin is null
      };

      try {
        const res = await axios.post(
          apiUrl + "getdatesSearchfilter",
          formData,
          {
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json", // Ensure the content type is JSON
            },
          }
        );

        // Assuming the response data is structured as `res.data.results`
        const respo = res.data.results;
        console.log("l");
        console.log(respo);

        // Update the state with the fetched data
        setallspeedDate(respo);
      } catch (err) {
        // Enhanced error handling
        if (err.response) {
          console.error("Response error:", err.response);
        } else if (err.request) {
          console.error("Request error:", err.request);
        } else {
          console.error("Error message:", err.message);
        }
      }
    };
    console.log(filters);
    // Call the async function inside useEffect
    if (filters === "All") {
      getAlldates();
    } else {
      fetchData();
    }
  }, [filters]);
  const handlesearchSpeeddate = (e) => {
    const value = e.target.value;

    if (value === "All") {
      if (e.target.checked) {
        // If "All" is checked, add "All" to the filters and remove "Male" and "Female"
        setFilters(["All"]);
      } else {
        // If "All" is unchecked, just remove "All" from the filters (retain others like Male, Female, etc.)
        setFilters(filters.filter((filter) => filter !== "All"));
      }
    } else {
      if (e.target.checked) {
        // If the specific checkbox is checked, add it to the filters
        setFilters((prevFilters) => [...prevFilters, value]);
      } else {
        // If the specific checkbox is unchecked, remove it from the filters
        setFilters((prevFilters) =>
          prevFilters.filter((filter) => filter !== value)
        );
      }

      // If either "Male" or "Female" is checked, uncheck "All"
      if (filters.includes("All") && value !== "All") {
        setFilters(filters.filter((filter) => filter !== "All"));
      }
    }
  };

  const handlebuttontoggal = (index) => {
    seteditDelete((prevIndex) => (prevIndex === index ? null : index));
  };
  const handlebuttonDelete = async (id) => {
    setspeedId(id);
    setisPopupOpen_delete(true);
    setdeleteMessage("Are you sure? You want delete this");
  };
  const closePopup_delete = () => {
    setisPopupOpen_delete(false);
  };
  const handlespeedPostDelete = async () => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null;
    let formData = {
      id: speedId,
      user_id: userLogin.id,
    };
    try {
      const res = await axios.post(apiUrl + "handlepostDelete", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      setisPopupOpen_delete(false);
      getAlldates();
    } catch (err) {
      console.error("Error posting comment:", err);
    }
  };

  const refreshpopup = async () => {
    handlespeedPostDelete();
  };
  const handlebuttonEdit = async (id) => {
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = storedUsername ? JSON.parse(storedUsername) : null; // Safely parse login data
    let formData = {
      user_id: userLogin.id,
      id: id,
    };
    setspeedIdedit(id);
    try {
      const res = await axios.post(apiUrl + "getdatesedit", formData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json", // Ensure the content type is JSON
        },
      });
      var respo = res.data;
      setspeedDateEdit(respo.result);
      openPopup1();
      console.log(respo);
    } catch (err) {
      // Enhanced error handling
      if (err.response) {
      } else if (err.request) {
        console.error("Request data:", err.request);
      } else {
        console.error("Error message:", err.message);
      }
    }
  };

  const formateTime = (dateString) => {
    if (!dateString) return null;

    // Parse the date as UTC
    const postDate = new Date(dateString);
    if (isNaN(postDate.getTime())) {
      console.error("Invalid date:", dateString);
      return "Invalid date";
    }

    // Get the current time in UTC
    const now = new Date();
    console.log(now);
    // Adjust the postDate by subtracting 1 hour (3600000ms)
    const adjustedPostDate = new Date(postDate.getTime() - 3600000);

    // Convert postDate to Europe/Oslo time correctly
    const osloPostTime = new Intl.DateTimeFormat("en-GB", {
      timeZone: "Europe/Oslo",
      hour: "2-digit",
      minute: "2-digit",
      hour12: false, // Ensures 24-hour format
    }).format(adjustedPostDate);

    // Calculate time difference in seconds, minutes, hours, days
    const timeDiff = now.getTime() - postDate.getTime();
    const seconds = Math.floor(timeDiff / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);
    const weeks = Math.floor(days / 7);
    const months = Math.floor(days / 30);
    const years = Math.floor(days / 365);

    // Generate formatted "time ago" output
    if (seconds < 60) return `${seconds} second${seconds !== 1 ? "s" : ""} ago`;
    if (minutes < 60) return `${minutes} minute${minutes !== 1 ? "s" : ""} ago`;
    if (hours < 24) return `${hours} hour${hours !== 1 ? "s" : ""} ago`;
    if (days === 1) return `Yesterday`;
    if (days < 7) return `${days} day${days !== 1 ? "s" : ""} ago`;
    if (weeks < 4) return `${weeks} week${weeks !== 1 ? "s" : ""} ago`;
    if (months < 12) return `${months} month${months !== 1 ? "s" : ""} ago`;
    return `${years} year${years !== 1 ? "s" : ""} ago`;
  };

  return (
    <>
      <Headertwo />
      <AlertDelete
        isOpen1={isPopupOpen_delete}
        onClose1={closePopup_delete}
        message={deleteMessage}
        onReturnSent={refreshpopup}
      ></AlertDelete>
      <section className="d-block searchmain gapy">
        <div className="container-lg">
          <div className="row gy-4">
            <div className="col-md-3 sticky-md-top h-100  evleft">
              <div className="d-flex flex-column gap-3 searchleftfilter scroll_bar pe-2 ">
                <h1>{t("Speeddate")}</h1>
                <div className="filtersearch">
                  <form action="">
                    <input
                      type="text"
                      value={searchTerm}
                      onInput={(e) => setSearchTerm(e.target.value)}
                      placeholder={t("Search_speeddate")}
                    />
                  </form>
                </div>
                <div className="d-flex flex-column gap-2">
                  <Link
                    to="/dashboard"
                    className="d-flex gap-3 align-items-center listoficons"
                  >
                    <div className="flex-shrink-0">
                      <div className="userpic userpic_main ofit rounded-circle ratio-unset overflow-hidden">
                        <IconHome
                          stroke={1}
                          className="w-100 h-100 main_homei"
                        />
                      </div>
                    </div>
                    <div className="flex-grow-1">
                      <h4>{t("Home")}</h4>
                    </div>
                  </Link>
                  <Link
                    to="/allspeeddate"
                    className={`d-flex gap-2 align-items-center filterlist ${
                      activeIndex === 0 ? "" : ""
                    }`}
                    onClick={() => handleButtonClick(0)}
                  >
                    <div className="flex-shrink-0">
                      <div className="eventicon fulw">
                        <img src={ev1} alt="eventicon" />
                      </div>
                    </div>
                    <div className="flex-grow-1">
                      <h3>{t("All_dates")}</h3>
                    </div>
                  </Link>
                  <Link
                    to="/speeddate"
                    className={`d-flex gap-2 align-items-center filterlist ${
                      activeIndex === 1 ? "active" : "active"
                    }`}
                    onClick={() => handleButtonClick(1)}
                  >
                    <div className="flex-shrink-0">
                      <div className="eventicon fulw">
                        <img src={ev2} alt="eventicon" />
                      </div>
                    </div>
                    <div className="flex-grow-1">
                      <h3>{t("Your_posted_dates")}</h3>
                    </div>
                  </Link>

                  <div className="newebentbtn d-block">
                    <button type="button" onClick={openPopup}>
                      {t("Create_new_date")}
                    </button>
                  </div>
                  <hr className="linehr" />
                </div>
              </div>
            </div>
            <div className="col-md-9">
              <div className="row gy-4">
                {isallspeedDate.length === 0 ? (
                  <div className="col-12 text-center">
                    <h2>{t("No_date_found")}</h2>
                  </div>
                ) : (
                  isallspeedDate.map((item, index) => (
                    <div key={index} className="col-sm-6 col-xl-4">
                      <div className="position-relative search_content">
                        <div className="d-block searchimg x">
                          <img
                            className="w-100 h-100 object-fit-cover"
                            src={item.image}
                            alt="img"
                          />
                        </div>
                        <div className="d-flex align-items-center gap-2 searchgender_main">
                          <div className="d-block searchwomen flex-shrink-0 d-flex align-items-center justify-content-between gap-1">
                            {item.gender === "Male" ? (
                              <img
                                className="w-100 h-100"
                                src={require("../assets/images/male.png")}
                                alt="Male"
                              />
                            ) : item.profile_type === "Single profile" &&
                              item.gender === "Female" ? (
                              <img
                                className="w-100 h-100"
                                src={require("../assets/images/woman.png")}
                                alt="Female"
                              />
                            ) : item.gender === "Couple" ? (
                              <>
                                <img
                                  className="w-100 h-100"
                                  src={require("../assets/images/male.png")}
                                  alt="Male"
                                />
                                <img
                                  className="w-100 h-100"
                                  src={require("../assets/images/woman.png")}
                                  alt="Female"
                                />
                              </>
                            ) : null}{" "}
                            {/* Add more conditions if necessary */}
                          </div>
                        </div>
                        <div className="d-flex align-items-start gap-3 justify-content-between px-3 searchusere">
                          <div className="d-flex justify-content-between h-100 flex-column gap-1 w-100">
                            <div className="d-flex flex-column gap-1 pb-2 w-100">
                              <Link
                                to={`/friend/${item.uid}`}
                                className="text1liness"
                              >
                                {item.username}
                              </Link>
                              <span>{calculateAge(item.birthday_date)}</span>
                              {item.location !== null &&
                                item.location !== "" && <p>{item.location}</p>}
                              <p className="discription_p scroll_bar">
                                {item.description}
                              </p>
                            </div>
                            {/* Assuming 'username' is a property of item */}
                            <div className="d-flex flex-column align-items-start gap-3 w-100">
                              {" "}
                              <p className="text-center w-100 sppeddate_time">
                                {formateTime(item.date)}
                              </p>
                              {/* Assuming 'description' is a property of item */}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>
            </div>
          </div>
        </div>
      </section>
      <Createnewdate
        isOpen={isPopupOpen}
        onClose={closePopup}
        refreshpage={refreshpage}
      ></Createnewdate>

      <Editnewdate
        isOpen={isPopupOpen1}
        onClose={closePopup1}
        speedDateEdit={speedDateEdit}
        refreshpage={refreshpage}
        speedIdedit={speedIdedit}
      ></Editnewdate>
      <Footer />
    </>
  );
}
